.contentBox {
    /* Rectangle 5281 */


    box-sizing: border-box;

    position: absolute;
    width: 390px;
    height: 583px;
    left: 0px;
    top: 261px;

    border: 5px solid #000000;
    border-radius: 20px;

}



.headLine {
    /* Rectangle 5282 */


    position: absolute;
    width: 390px;
    height: 58px;
    left: 0px;
    top: 260px;

    background: #000000;
}

.headLeftButton {
    /* button.comp */


    /* Auto layout */

    display: flex;
    flex-direction: row;
    align-items: flex-start;
    padding: 0px;
    gap: 8px;
    line-height: 58px;

    position: absolute;
    width: 58px;
    height: 14px;
    left: 19px;
    top: 20px;
}

.headLine .title {
    /* title.block */


    /* Auto layout */

    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin: 0px auto;
    padding: 16px 0 0 0;
    gap: 16px;

    /* title.txt */


    width: 130px;
    height: 24px;

    font-family: 'IBM Plex Mono';
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    /* identical to box height, or 100% */

    text-transform: uppercase;

    /* Greyscale/ W */

    color: #FFFFFF;


    /* Inside auto layout */

    flex: none;
    order: 0;
    flex-grow: 0;


}

.personIcon {
    /* Person */


    position: absolute;
